@use 'sass:map';

// global > breakpoint
// ------------------------------
$breakpoints: (
    'sm': 'screen and (min-width: 415px)',
    'md': 'screen and (min-width: 768px)',
    'xl': 'screen and (min-width: 1025px)',
    'xxl': 'screen and (min-width: 1601px)',
);

// @mixin mq($breakpoint) {
//     @media #{map-get($breakpoints, $breakpoint)} {
//         @content;
//     }
// }
@mixin mq($breakpoint: "md") {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}
